import React, { useState, useEffect } from 'react';
import { List, ListItem, ListItemButton, ListItemAvatar, Avatar, ListItemText } from '@mui/material';
import { useTelegram } from '../../hooks/useTelegram';
import cardBack from '../../images/deck/card-back.png';
import { deck } from '../../helpers/deck';
import { socket } from '../../soket/soket';
import { useAppSelector } from '../../hooks/useAppSelector';
import { useActions } from '../../hooks/useActions';
import { urlParams } from '../../helpers/urlParams';



const Players = () => {
    const { user } = useTelegram();
    const { players } = useAppSelector(state => state.gameReducer);
    const { updatePlayers, updateBank } = useActions();

    const [cards, setCards] = useState([]);

    useEffect(() => {
        socket.emit('player-connection', {
            name: user?.first_name!,
            photo: urlParams('photo_url'),
            userId: user?.id!,
            isUserTurn: false,
            isFold: false
        });
    }, [user]);

    useEffect(() => {
        socket.on('player-connection', (data) => {
            updatePlayers(data);
        });
 
        socket.on('move', (data) => {
            updatePlayers(data.players);
            updateBank(data.bank);
        });
    
        socket.on('get-cards', (data) => {
            setCards(data);
        });

        socket.on('new-game', (data) => {
            updatePlayers(data.players);
            updateBank(data.bank);
        });
    }, []);

    useEffect(() => {
        const showWinner = (data: any) => {
            const message = data === socket.id ? 'Ви перемогли' : `Користувач ${players[data].name} переміг`;
            window.Telegram.WebApp.showAlert(message);
        }
        socket.on('win', showWinner);
        return () => {
            socket.off('win', showWinner);
        }

    }, [players])

    return (
        <List dense sx={{ width: '100%', bgcolor: 'transparent' }}>
            {Object.values(players).map((player, i) => {
                const you = player.userId === user?.id ? '(Ви)' : '';
                return (
                    <ListItem
                        key={`player-${player.userId}-${i}`}
                        disablePadding
                        secondaryAction = {
                            <div>
                                {!player.isFold && <>
                                    {cards.map((card, i) => <img 
                                        src={player.userId === user?.id ? deck[card] : cardBack} 
                                        key={`card-${player.userId}-${i}`} 
                                        alt='Playing card'
                                    />)}                                   
                                </>}
                            </div>
                        }
                    >
                        <ListItemButton>
                            <ListItemAvatar>
                                <Avatar
                                    alt={`avatar-${player.userId}-${i}`}
                                    src={player.photo}
                                    sx={player.isUserTurn ? {
                                        border: '3px solid green'
                                    }: undefined}
                                >{player.name[0]}</Avatar>
                            </ListItemAvatar>
                            <ListItemText primary={`${player.name}${you}`}/>
                        </ListItemButton>
                    </ListItem>
                );
            })}
        </List>
    );
};

export default Players;