import React, { useState } from 'react';
import { Slider, Button } from '@mui/material';
import { useAppSelector } from '../../hooks/useAppSelector';
import { socket } from '../../soket/soket';
import styles from './Controls.module.css';


const Controls = () => {
    const { players } = useAppSelector(state => state.gameReducer);
    const [bet, setBet] = useState<number>(5);
    const [minBet, setMinBet] = useState<number>(5);

    const onChangeBet = (e: Event, value: number | number[]) => {
        const num = value as number;
        if(num >= minBet) setBet(num);
    }

    const call = () => {
        socket.emit('move', bet);
    }

    const fold = () => {
        socket.emit('fold', true);
    }

    const check = () => {
        socket.emit('check', true);
    }

    return (
        <div className={styles.controls}>
            <div className={styles.bet}>
                <Slider 
                    min={5}
                    max={100}
                    value={bet}
                    step={5}
                    valueLabelDisplay='on'
                    disabled={!players[socket.id]?.isUserTurn}
                    onChange={onChangeBet}
                    sx={{
                        height: 5,
                        color: 'success.main'
                    }}
                />
            </div>
            <div className={styles.choise}>
                <Button 
                    variant='contained' 
                    color='success' 
                    className={styles.one}
                    disabled={!players[socket.id]?.isUserTurn}
                    onClick={call}
                    sx={{
                        ":disabled": {
                            background: '#2e7d32',
                            color: '#fff',
                            opacity: 0.5
                        }
                    }}
                >Поставити</Button>
                <Button 
                    variant='contained' 
                    color='error'
                    disabled={!players[socket.id]?.isUserTurn}
                    onClick={fold}
                    sx={{
                        ":disabled": {
                            background: '#d32f2f',
                            color: '#fff',
                            opacity: 0.5
                        }
                    }}
                >Впасти</Button>
                <Button 
                    variant='contained' 
                    color='warning'
                    disabled={!players[socket.id]?.isUserTurn}
                    onClick={check}
                    sx={{
                        ":disabled": {
                            background: '#ed6c02',
                            color: '#fff',
                            opacity: 0.5
                        }
                    }}
                >Звіритись</Button>
            </div>
        </div>
    );
};

export default Controls;