import { PayloadAction, createSlice } from "@reduxjs/toolkit"
import { IPlayer } from "../../models/IPlayer";


interface IPlayers {
    [key: string]: IPlayer;
}

interface IGame {
    players: IPlayers;
    bank: number;
}

const initialState: IGame = {
    players: {},
    bank: 0
}


export const game = createSlice({
    name: 'game',
    initialState,
    reducers: {
        updatePlayers: (state, action: PayloadAction<IPlayers>) => {
            return {
                ...state,
                players: action.payload
            }
        },
        updateBank: (state, action: PayloadAction<number>) => {
            return {
                ...state,
                bank: action.payload
            }
        },
    }
});

export const { actions, reducer } = game;